body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

#root {
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.canvas-app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.toolbar {
  display: flex;
  align-items: center;
  padding: 8px 15px;
  background-color: #f5f5f5;
  border-bottom: 1px solid #ddd;
  gap: 15px;
  min-height: 44px;
}

.canvas-container {
  display: flex;
  flex: 1;
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.mode-selector {
  display: flex;
}

.mode-toggle-button {
  padding: 8px 16px;
  border: 1px solid #007bff;
  background: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
}

.mode-toggle-button:hover {
  background: #0056b3;
  border-color: #0056b3;
}

.toolbar-section {
  display: flex;
  align-items: center;
  gap: 10px;
}

.property-panel {
  position: absolute;
  right: 10px;
  top: 10px;
  background: white;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 15px;
  min-width: 200px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.property-panel h3 {
  margin: 0 0 10px 0;
  font-size: 14px;
}

.property-panel .property-group {
  margin-bottom: 10px;
}

.property-panel label {
  display: block;
  font-size: 12px;
  margin-bottom: 4px;
}

.property-panel input {
  width: 100%;
  padding: 4px;
  border: 1px solid #ccc;
  border-radius: 2px;
}

.property-panel button {
  width: 100%;
  padding: 8px;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

.property-panel button:hover {
  background: #c82333;
}

.add-rectangle-btn {
  padding: 8px 16px;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.add-rectangle-btn:hover:not(:disabled) {
  background: #218838;
}

.add-rectangle-btn:disabled {
  background: #6c757d;
  cursor: not-allowed;
  opacity: 0.5;
}

.canvas-info {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 2px 6px;
  border-radius: 3px;
  font-size: 12px;
  line-height: 1.2;
  height: 18px;
  min-height: 18px;
  max-height: 18px;
  display: flex;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
}